@import "variables";
@import "mixins";
@import "fonts";

html, body {
  margin: 0;
}

body {
  background: $bg-color;
  color: $font-color;
  font-family: $font-family-main;
  font-size: 14px;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizelegibility;
  overflow-x: hidden;
  transition: $transition;
  outline: none;
  // overflow-y: hidden;

  .light {

    h1, h2, h3, h4, h5, h6 {
      color: $font-color-headers-light!important;
    }

    p {
      color: $font-color-light!important;
      font-weight: bold;

      & .graph {
        position: relative;

        &:after {
          position: absolute;
          right: -25px;
          top: 3px;
          content: '';
          width: 16px;
          height: 11px;
          background-image: url(../assets/images/icons/graph.svg);
          background-position: center;
          background-size: 100%;
          background-repeat: no-repeat;
        }
      }
    }

    hr {
      border-color: rgba(255, 255, 255, 0.15);
    }

    a {
      &.transparent {
        color: #fff;
        border-bottom: 1px dashed #fff;
        opacity: .7;

        &:hover {
          opacity: 1;
        }
      }
    }

    form {

      &.form-linear {

        .form-group { 

          &.has-error {
            position: relative;

            .input-group-append {
              position: relative;

              &:before {
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: -33px;
                width: 18px;
                height: 100%;
                background-image: url('../assets/images/icons/warning.svg');
                background-repeat: no-repeat;
                background-position: center;
              }
            }
          }
        }
      }

      &.form-block {

        .form-group { 

          &.has-error {

            .form-control {
              box-shadow: 0 0 0 2px #e0a8a8;
            }

            .input-group {
              .input-group-prepend {
                .input-group-text {
                  box-shadow: 0 0 0 2px #e0a8a8;
                }
              }
            }
          }
        }
      }
    }

    .form-group {
      color: #fff;
      margin-bottom: 20px;

      label {
        font-size: 1.142em;

        .required {
          color: $font-color-light;
        }
      }

      .text-muted {
        color: $font-color-light!important;
        font-weight: bold;
      }
    }
  }

  header {
    position: relative;
    height: 95vh;
    
    .navbar {
      padding: 50px 0;
    }

    .header-bg {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      background-image: url('../assets/images/header-bg.jpg');
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;

      &.header-bg-faded:after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        background-color: rgba(0, 0, 0, 0.5);
      }
    }

    .offset-top {
      margin-top: 150px;
    }

    .moved {
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 50%);
      z-index: 10;

      .row:not(.owl-carousel) {
        padding-left: 15px; 
        padding-right: 15px;
      }

      .row.owl-carousel {
        margin: 0;
      }
    }

    .form-control {
      background-color: #fff!important;
    }

    .input-group {

    .input-group-prepend {

      .input-group-text {
        border: none;
        border-right: 2px solid $bg-color;
      }
    }
  }
  }

  .form-control {
    outline: none!important;
    border: none!important;
    border-radius: 2px;
    font-size: 0.92em;
    font-weight: bold;
    padding-left: 10px;
    background-color: $bg-color;
    color: $font-color-dark;


    &::-webkit-input-placeholder {
      color: #BCBCBC;
    }
    &::-moz-placeholder {
      color: #BCBCBC;
    }
    &:-ms-input-placeholder { 
      color: #BCBCBC;
    }
    &:-moz-placeholder { 
      color: #BCBCBC;
    }

    &:focus, &.focus {
      outline: none!important;
      box-shadow: none!important;
      border: inherit!important;
      background-color: $bg-color;
    }
  }

  .input-group {

    .input-group-prepend {

      .input-group-text {
        background-color: #fff!important;
        border-color: #fff;
        border: 1px solid $bg-color;
        font-size: 0.92em;
        font-weight: bold;
        color: $font-color;
      }
    }
  }

  .owl-carousel {

    .owl-dots {
      margin-top: 15px!important;

      .owl-dot {

        span {
          width: 7px;
          height: 7px;
          margin: 5px 6px;
          background: #e8e8e8;
          transition: $transition;
        }

        &.active {

          span {
            background: #bdbdbd;
          }
        }
      }
    }

    .owl-stage {
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;

      -webkit-flex-wrap: wrap;
      -ms-flex-wrap: wrap;
      flex-wrap: wrap;
    }

    .owl-item{
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      height: auto !important;
    }

    &.header-features {
      .owl-dots {
        position: absolute;
        bottom: 0.375em;
        right: 0px;
      }
    }
  }

  .modal {

    .modal-dialog {

      .modal-content {
        border-radius: 4px;
        border: none;

        &:after {
          content: '';
          position: absolute;
          top: 5px;
          left: 5px;
          width: calc(100% - 10px);
          height: calc(100% - 10px);
          border-radius: 4px;
          border: 1px solid #F7F7F7;
          pointer-events: none;
        }

        .modal-header {
          border: none;
          padding-bottom: 0em;
        }

        .modal-body {
           p, ul {
            font-size: 0.9em;
           }
        }
      }
    }
  }

  .box {
    padding: 30px 30px 25px;
    border-radius: 4px;
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 5px;
      left: 5px;
      width: calc(100% - 10px);
      height: calc(100% - 10px);
      border-radius: 4px;
      border: 1px solid #F7F7F7;
    }

    &.box-white {
      background-color: #fff;
    }

    &.box-features {
      padding-left: 120px;

      &:after {
        content: attr(data-feature-number);
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(-25%, -10px);
        font-size: 100px;
        font-weight: bold;
      }

      &:nth-child(2)::after {
        color: #3E76E0;
      }

      &:nth-child(3)::after {
        color: #3EB4E0;
      }

      &:nth-child(4)::after {
        color: #3EE0DA;
      }

      &:nth-child(5)::after {
        color: #3EE0A8;
      }
    }

    h1, h2, h3, h4, h5, h6 {
      color: $font-color-headers!important;
    }

    p {
      color: $font-color-dark!important;
    }
  }

  .card {
    border: none;
    overflow: hidden;

    .card-header {
      background-color: #FCFCFC;
      padding: 25px 30px 20px;
      border-bottom: 1px solid #efefef;

      h3 {
        font-family: $font-family-main;
        font-weight: bold;
        text-transform: unset;
        font-size: 1.57em;
        margin: 0;
        line-height: 1;
      }
    }

    .card-body {
      padding: 25px 30px 20px;

      .card-title {
        font-family: $font-family-main;
        font-weight: bold;
        text-transform: unset;
        font-size: 1.57em;
      }

      .card-subtitle {
        color: $font-color-dark;
      }

      .card-text {
        &.card-text-overflow {
          overflow: hidden;
          -o-text-overflow: ellipsis;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 4;
          -webkit-box-orient: vertical;
        }
      }

      &.card-body-map {
        padding: 0!important;
      }

      &.card-body-gray {
        background-color: #FCFCFC;
      }
    }

    .card-footer {
      background-color: #fff;
      padding: 15px 30px 15px;
      border-top: 1px solid #efefef;

      .card-price {
        font-weight: bold;
        font-size: 1.1em;
      }
    }
  }

  .property {
    display: flex;
    flex-direction: row;
    background-color: $bg-color-white;
    border-radius: 4px;
    overflow: hidden;
    box-shadow: 0 15px 10px -10px rgba(0, 0, 0, 0.1);
    margin-bottom: 50px;

    &:last-child {
      margin-bottom: 20px;
    }

    .property-images {
      width: 400px;
      height: 100%;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .property-body {
      padding: 30px 30px 25px;

      .property-title {

        h3 {
          font-family: $font-family-main;
          font-weight: bold;
          font-size: 1.57em;
          text-transform: unset;
        }

        .property-body-adress {
          color: $font-color-dark;
        }
      }

      .property-description {
        margin-bottom: 20px;

        p {
          text-align: justify;
          overflow: hidden;
          -o-text-overflow: ellipsis;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 4;
          -webkit-box-orient: vertical;
        }
      }

      .property-characteristics {
        display: flex;
        flex-direction: row;

        .property-characteristics-item {
          padding-right: 50px;

          &:last-child {
            padding-right: 0;
          }
        }

        h5, h6 {
          text-align: center;
          font-family: $font-family-main;
          font-weight: bold;
          text-transform: unset;
        }

        h5 {
          font-size: 1.5em;
        }

         h6 {
          color: $font-color-dark;
          font-size: 0.9em;
        }
      }
    }
  }

  .characteristics {
    display: flex;
    align-items: flex-end;
    white-space: nowrap;
    line-height: 1;
    margin-bottom: 20px;

    .key {

    }

    .line {
      width: 100%;
      height: 0px;
      border-bottom: 1px dashed #DDDDDD;
      margin-right: 20px;
      margin-left: 20px;
    }

    .value {
      font-weight: bold;
    }
  }

  .property-video-btn {
    width: 133px;
    height: 133px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    color: #fff;
    background-image: url('../assets/images/properties/video-button.jpg');
    background-position: center top;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    transition: all 0.2s ease-in-out;
    cursor: pointer;

    .fa {
        transition: transform 0.2s ease-in-out;
        transform: scale(1);
      }

    &:hover {

      .fa {
        transform: scale(1.2);
      }

      &:after {
        border-width: 0px;
      }
    }

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      border: 6px solid rgba(255, 255, 255, 0.5);
      transition: border-width 0.2s ease-in-out;
    }

    &:before {
      content: 'Видео-презентация';
      position: absolute;
      top: 50%;
      left: 0;
      width: auto;
      height: auto;
      padding: 15px 85px;
      z-index: -1;
      font-size: 16px;
      font-weight: bold;
      white-space: nowrap;
      border-radius: 40px;
      background-color: #18C77E !important;
      border-color: #18C77E !important;
      box-shadow: 0 10px 20px -5px rgba(113, 222, 176, 0.5);
      transform: translate(calc(-100% + 30px), -50%);
      cursor: pointer;
      pointer-events: all;
    }
  }

  .filters {
    display: flex;

    .filter {
      cursor: pointer;
      color: $font-color-dark;

      &:hover {
        color: $font-color;
      }

      &.active {
        color: $font-color;

        .filter-direction {
          visibility: visible;
        }
      }

      .filter-direction {
        visibility: hidden;
      }
    }
  }

  .masonry-with-columns {
    columns: 2 100px;
    column-gap: 1rem;

    div {
      width: 100px;
      margin: 0 1rem 1rem 0;
      display: inline-block;
      width: 100%;
      height: 200px;

      &:nth-child(2n-1), &:last-child {
        height: 300px;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 3px;
      }
    } 
  }

  &.page-404 {
    background-color: $bg-color-primary;
    color: $font-color-light;
    text-align: center;
    height: 100vh;

    .heading-404 {
      font-size: 200px;
      font-family: $font-family-headers;
      color: #fff;
      line-height: 1;
      margin-top: 20px;
      margin-bottom: 20px;
    }

    p {
      color: #fff;
      margin-bottom: 30px;
    }
  }

  .text-group {
    margin-bottom: 15px;

    label {
      color: $font-color-dark;
      margin-bottom: 5px;
    }

    h5 {
      font-size: 1.28em;
      font-family: $font-family-main;
      font-weight: bold;
      text-transform: unset;
    }
  }
}

section {
  padding: 75px 0 85px;
  position: relative;

  .container {
    z-index: 10;
    position: relative;
  }

  &.white {
    background-color: #fff!important;
  }

  &.objects {
    padding-top: 130px;

    .objects-tiles {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: 1fr 1fr;

      .tile {
        height: 300px;

        .tile-wrapper {
          position: relative;
          width: 100%;
          height: 100%;
          transform-style: preserve-3d;
          transition: .5s;
          cursor: pointer;

          .tile-front {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 0;
            overflow: hidden;

            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
          }

          .tile-back {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 1;
            background-color: #ffffff;
            transition: $transition;
            opacity: 0;
            padding: 30px;

            p {
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-line-clamp: 6;
              -webkit-box-orient: vertical;
            }
          }

          &:hover .tile-back {
            opacity: 1;
          }
        }

        h5, h6 {
          font-family: $font-family-main;
        }

        h5 {
          font-size: 1.57em;
          font-weight: 600;
          text-transform: unset;
        }

        h6 {
          font-size: 1em;
          font-weight: normal;
          text-transform: unset;
          color: $font-color-dark;
        }
      }
    }
  }

  &.features {

    .blured-bg:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #E5E5E5;
      filter: blur(40px);
    }
  }

  &.golf {

    padding-bottom: 200px;

    .golf-images {
      position: relative;

      img {
        width: 100%;
        position: absolute;

        &:nth-child(1) {
          top: 100px;
          right: 0;
          z-index: 2;
        }

        &:nth-child(2) {
          top: 0px;
          right: -100px;
          z-index: 1;
        }
      }
    }

    .caption:after {
      content: '';
      position: absolute;
      bottom: -30px;
      right: 0;
      width: 100px;
      height: 10px;
      border-radius: 2px;
      border: 1px solid #E8E8E8;
      border-top: none;
      border-right: none;
    }
  }

  &.free-visit {
    position: relative;

    .free-visit-bg {
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      background-image: url('../assets/images/free-visit.jpg');
      background-position: center top;
      background-repeat: no-repeat;
      background-size: cover;
    }
  }

  &.map {

    .map-frame {
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
    }

    .box {
      box-shadow: 0 20px 30px -15px #afafaf;
    }

    .container {
      pointer-events: none;

      .box {
        pointer-events: all!important;
      }
    }
  }

  &.moved-top {
    margin-bottom: -200px;

    .container {
      transform: translateY(-200px);
    }
  }

  &.property-photos {
    padding: 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;

    div {

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

footer {
  position: relative;
  z-index: 0;
  padding: 75px 0 85px;
  background-color: $bg-color-white;

  &.sub {
    background-color: $bg-color;
    padding: 10px 0;
    color: $font-color-dark;

    a {
      color: $font-color-dark;
    }
  }
}

h1, h2, h3, h4, h5, h6 {
  font-family: $font-family-headers;
  color: $font-color-headers;
  line-height: normal;
  position: relative;
  text-transform: uppercase;
}

h1 {
  font-size: 2.9em;
  text-transform: uppercase;
}

h2 {
  font-size: 1.714em;
  margin-bottom: 15px;
}

.h2-subheading {
  font-size: 1em;
  color: $font-color-primary;
  display: block;
  margin-bottom: 5px;

  &.green {
    color: $font-color-green;
  }
}

h3 {
  font-size: 1.283em;
  margin-bottom: 15px;

  &[data-bg-text]:before {
    content: attr(data-bg-text);
    position: absolute;
    font-size: 8em;
    color: #A0A0A0;
    opacity: .05;
    top: 0;
    left: 0;
    transform: translate(-25%, -50%);
  }
}

.h3-subheading {
  font-size: 0.714em;
  color: $font-color-primary;
  margin-top: -15px;
  display: block;
  margin-bottom: 15px;
}

h4 {
  font-size: 1.1em;
}

h5 {
  font-size: 1.1em;
}

p {
  margin-bottom: 15px;

  &.small {
    font-size: 0.785em;
    opacity: 0.7;
  }

  &.caption {
    position: relative;
    font-size: 0.785em;
    font-style: italic;
    font-weight: normal;
  }
}

button {
  text-decoration: none!important;
  outline: none!important;
}

a {
  text-decoration: none!important;
  color: $font-color;
  transition: $transition;

  &:not(.btn) {
    &:hover, &:active {
      color: $font-color;
      opacity: 0.8;
      outline: none!important;
    }

    &:focus {
      color: $font-color;
      outline: none!important;
    }
  }

  &[href^="tel"] {
    color: inherit;
    text-decoration: none;
  }
}

ul {

  &.colored {
    list-style: none;
    position: relative;
    padding-left: 30px;

    li {
      position: relative;

      &:before {
        content: '';
        position: absolute;
        top: 7px;
        left: -15px;
        width: 5px;
        height: 7px;
        background-image: url('../assets/images/icons/chevron-right.svg');
        background-repeat: no-repeat;
        background-size: 100%;
        background-position: center left;
      }
    } 
  }

  &.rect {

    li {

      &:before {
        content: '';
        position: absolute;
        top: 7px;
        left: -15px;
        width: 5px;
        height: 5px;
        background-color: $bg-color-primary;
        transform: rotate(45deg);
      }
    } 
  }
}




@import "buttons";
@import "responsive";
